import alt from '../alt';
import uuid from 'node-uuid';
import ProductStore from './ProductStore';

function createCategory(title, children) {
  return {
    id: uuid.v4(),
    title: title,
    children: children.map(item => item.id)
  }
}

function createCategories() {
  var names = [
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        ''
      ],
      categories = [],
      indexes = {},
      menu = [],
      cat;
  var { products } = ProductStore.getState();

  for (let i in names) {
    let name = names[i],
        cats = [],
        cat;

    i = +i;

    for (let j = 0; j < 2; ++j) {
      let bgn = i * 20 + j * 10,
          end = bgn + 10,
          cat;
      cat = createCategory(`${name}${j + 1}`, products.slice(bgn, end));
      indexes[cat.id] = categories.length;
      categories.push(cat);
      cats.push(cat);
    }

    cat = createCategory(name, cats);
    menu.push(cat);
    indexes[cat.id] = categories.length;
    categories.push(cat);
  }

  cat = createCategory('', menu);
  indexes[cat.id] = categories.length;
  categories.push(cat);

  return { indexes, categories };
}



class CategoryStore {
  constructor() {
    var { indexes, categories } = createCategories();

    this.indexes    = indexes;
    this.categories = categories;
  }
}

export default alt.createStore(CategoryStore);




